import React, { Component } from "react";
import CardLayout from "../../../components/Cards/CardLayout";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import { FormGroup, Col, Button } from "reactstrap";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import "react-bootstrap-table/dist/react-bootstrap-table.min.css";
import { Link } from "react-router-dom";
import Loader from '../../../components/Loader/Loader';

class StatesList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedState: "",
      modalFlag : false,
      stateToEdit : {},
      loading : true
    };
  }
  componentWillMount() {
    this.props.getStatesList();
    let compRef =this;
   setTimeout(() => {
  this.setState({
    loading :false
  })
   },3000);
  }
  onStateValueChange(value) {
    this.setState({
      selectedState: value.value
    });
  }

  onDeleteState(cell, row) {
    let componentRef = this;
    return (
      <Link to={this} >
        <i className="fa fa-trash" title="Delete" />
      </Link>
    );
    //onClick={() => componentRef.deleteConfirm(row._id)}
  }

  onEditState(cell, row) {
    return (
      <Link to={`${this.props.match.url}/stateForm/${row.Id}`}>
        <i className="fa fa-pencil" title="Edit" />
      </Link>
    );
  }
  render() {
    const sortingOptions = {
      defaultSortName: "StateName",
      defaultSortOrder: "asc",
      sizePerPageList: [
        {
          text: "5",
          value: 5
        },
        {
          text: "10",
          value: 10
        },
        {
          text: "20",
          value: 20
        },
        {
          text: "All",
          value: this.props.states.length
        }
      ],
      sizePerPage: 5
    };
    return (
      this.state.loading ? <Loader loading={this.state.loading} /> :
      (
        <div style={{margin : 30}}>
        <CardLayout name="States">
          <FormGroup row>
            <Col xs="12" >
            <BootstrapTable
              ref="table"
              data={this.props.states}
              pagination={true}
              search={true}
              options={sortingOptions}
              //exportCSV={true}
              csvFileName="States List"
            >
              <TableHeaderColumn dataField="_id" headerAlign="left" isKey hidden>
                Id
              </TableHeaderColumn>
              <TableHeaderColumn
                dataField="StateName"
                headerAlign="left"
                width="60"
                csvHeader="State Name"
                dataSort={true}
              >
                State Name
              </TableHeaderColumn>
              <TableHeaderColumn
                dataField="StateCode"
                headerAlign="left"
                width="40"
                csvHeader="Code"
                dataSort={true}
              >
                Code
              </TableHeaderColumn>
              <TableHeaderColumn
              dataField="edit"
              dataFormat={this.onEditState.bind(this)}
              headerAlign="left"
              width="20"
              export={false}
              >
                Edit
              </TableHeaderColumn>
              <TableHeaderColumn
              dataField="delete"
              dataFormat={this.onDeleteState.bind(this)}
              headerAlign="left"
              width="20"
              export={false}
              >
                Delete
              </TableHeaderColumn>
            </BootstrapTable>
            </Col>
          </FormGroup>
        </CardLayout>
        </div>
      )
     
    );
  }
}
 const mapStateToProps = state => {
  return {
    statesList: state.stateReducer.statesList,
    states: state.stateReducer.states
  };
};

 const mapDispatchToProps = dispatch => {
  return {
    getStatesList: () => dispatch(actions.getStatesList())
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(StatesList);



