import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import CardLayout from "../../../components/Cards/CardLayout";
import { FormGroup, Col, Button, Label } from "reactstrap";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import InputElement from "../../../components/InputElement/InputElement";
class StatesForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      updateFlag: false,
      currentState: {},
      selectedState: ""
    };
  }
  componentDidMount() {
    if (this.props.match.params.id !== undefined) {
      let currentState = this.props.statesData.find(
        state => state.Id == this.props.match.params.id
      );
      let selectedState = this.props.statesList.find(
        state => state.value == this.props.match.params.id
      );
      if (currentState) {
        this.setState({
          updateFlag: true,
          currentState: currentState,
          selectedState: selectedState
        });
      }
    }
  }
  onStateSelectChange(value) {
    this.setState({
      selectedState: value
    });
  }
  onStateCodeChange(value){
    console.log("state code", value);
  }
  render() {
    const {currentState} = this.state;
    const valueSelect = this.state.selectedState;
    return (
      <CardLayout name="State Form">
        <FormGroup row />
        <FormGroup row>
          <Col xs="8" md="4">
          <Label />
            <DropdownSelect
              name="States"
              placeholder="Select State..."
              options={this.props.statesList}
              value={valueSelect}
              onChange={this.onStateSelectChange.bind(this)}
              simpleValue
            />
          </Col>
          <Col  md="4">
            <InputElement
              type = "text"
              placeholder = "Please enter state code"
              value = {this.state.currentState.StateCode}
              onChange = {(stateCode) => this.onStateCodeChange(stateCode)}
            />
          </Col>
        </FormGroup>
        <FormGroup row>
          <Col md="3">
           <Button className="theme-btn-color-white" >Edit</Button>
           </Col>
          </FormGroup>
      </CardLayout>
    );
  }
}
const mapStateToProps = state => {
  return {
    statesList: state.stateReducer.statesList,
    statesData: state.stateReducer.states
  };
};

const mapDispatchToProps = dispatch => {
  return {};
};
export default connect(mapStateToProps, mapDispatchToProps)(StatesForm);



